@import url(https://fonts.googleapis.com/css?family=Roboto:300);
.CountUp{
    color: #fff;
    font-size: 1.25rem;
    font-weight: bold;
}
.pomodoro{
    font-size: 1.3rem;
    text-align: center;
    padding: 10px 5px;
    background: black;
    border-radius: 30px;
    width:30%;
    margin-top: 5%;
    margin-left: 73%;
 position: absolute;
}
.pomodoro .timer{
    color: white;
    font-weight: bold;
}


#ok-btn {
  padding: 10px 85px;
}

.form-container {
  background-color: white;
  margin: auto;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2),
    0 5px 5px 0 rgba(255, 124, 2, 0.733);
  padding: 10px;
  border-radius: 10px;
  font-weight: 100px;
  border-color: #ffa4a4;
  display: flex;
    justify-content: center;
    align-items: center;
    font-family: sans-serif;
    text-align: center;
    background-color: white;
    z-index: 100000;
}

.player-info-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
}

.success-message {
  font-family: "Roboto", sans-serif;
  background-color: rgba(88, 73, 58, 0.397);
  border-radius: 10px;
  padding: 15px;
  color: white;
}

.form-field {
  margin: 10px 0 10px 0;
  padding: 15px;
  font-size: 14px;
  border: 0;
  font-family: "Roboto", sans-serif;
  display: block;
  align-content: center;
}

span {
  font-family: "Roboto", sans-serif;
  font-size: 14px;
  color: red;
  margin-bottom: 15px;
}

input {
  background: #f2f2f2;
  border-radius: 10px;
}

.error {
  border-style: solid;
  border: 2px solid #ffa4a4;
}

.form-container .player-info-form .btn-group .btn-submit,

.form-container  .success-message .btn-out
 {
  background: #4caf50;
  color: white;
  cursor: pointer;
  font-weight: bold;
  border-radius: 10px;
  font-size: 10px;
  display: inline-block;
}
.form-container .player-info-form .btn-group .btn-exit{
  color: white;
  cursor: pointer;
  font-weight: bold;
  border-radius: 10px;
  font-size: 10px;
  display: inline-block;
  background: gray
}
button:disabled {
  cursor: default;
}
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
}

.modal-main {
  position: fixed;
  background: white;

  height: auto;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.display-block {
    z-index: 100000;
  display: block;
}

.display-none {
  display: none;
}

.container {
 top:0;
  left: 0;
  z-index: 1;
  height: 17vh;
  position: absolute;
  width: 20vw;
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: 1px solid #383838;
  border-end-end-radius: 25px;
}

.messages {
    padding: 1% 0;
    overflow: auto;
    flex: auto;
  }
.messageBox {
    background: none;
    border-radius: 20px;
    padding: 0px 5px;
    color: white;
    display: inline-block;
    max-width: 80%;
  }
  
  .messageText {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 0.6em;
    word-wrap: break-word;
  }
  
  .messageText img {
    vertical-align: middle;
  }
  .Admin{
    opacity: 0.5;
    font-size: 0.5rem;
  }
  .messageContainer {
    display: flex;
    justify-content: flex-end;
    padding: 0 5%;
    margin-top: 3px;
  }
  
  .sentText {
    font-size: 0.6rem;
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: rgb(68, 248, 83);
    letter-spacing: 0.3px;
  }
  
  .pl-10 {
    padding-left: 10px;
  } 
  
  .pr-10 {
    padding-right: 10px;
  }
  
  .justifyStart {
    justify-content: flex-start;
  }
  
  .justifyEnd {
    justify-content: flex-end;
  }
  
  .colorWhite {
    color: white;
  }
  
  .colorDark {
    color: white;
  }
  
  .backgroundDark {
    background: #383838;
  }
  
  .backgroundLight {
    background: #F3F3F3;
  }
.form {
    height: 25px;
    display: flex;
    border-radius: 8px;
    opacity: 0.8;
}

.input {
    background: #f2f4f4;
    border-start-end-radius: 100px;
    padding: 1%;
    width: 75%;
    height: 100%;
    font-size: 0.8rem;
}

input:focus,
textarea:focus,
select:focus {
    outline: none;
}

input:placeholder-shown {
    font-style: italic !important;
    font-size: 10px;
}

.sendButton {
    border-start-start-radius: 141px;
    opacity: 1;
    left: 75%;
    height: 20%;
    position: absolute;
    font-size: 10px;
    font-weight: bold;
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #383838;
    padding: 10px;
    display: inline-block;
    border: none;
    width: 25.5%;
    cursor: pointer;
    border-end-end-radius: 120px;
}

.sendButton:hover {
    background: black;
    opacity: 1;
}

.aside.emoji-picker-react {
    position: absolute
}

.emojibutton {
    background: #333;
    border: none;
    cursor: pointer;
}

.emojibutton:hover {
    background: black;
}
